<template>
    <div class="home">
        <div class="content">
            <div class="content_center">
                <el-input v-model="searchVal" placeholder="搜文件"></el-input>
                <div class="notice">
                    <p>公告：软件基本都是解锁版！在APP内更新会导致解锁失效，若遇到失效或使用异常的，就是被和谐了，等待更新即可（软件内的广告不要信）</p>
                </div>
                <div class="software-item" v-for="(item, index) in dataList" :key="index"
                    @click="(handleClickTo(item))">
                    <div class="software-item-left">
                        <div style="font-size: 18px;font-weight: bold;">{{ item.name }}</div>
                        <div style="color: #666;font-size: 14px;">{{ getTimes(item.time) }}</div>
                    </div>
                    <div class="software-item-right">
                        <i class="el-icon-download" style="font-size: 27px;"></i>
                    </div>
                </div>

            </div>

        </div>
        <div class="mask"></div>
        <div class="nav">
            <ul @click="handleClickNav">
                <li id="ys" :class="{ 'active': isActive == 'ys' }">影视</li>
                <li id="gj" :class="{ 'active': isActive == 'gj' }">工具</li>
                <li id="sj" :class="{ 'active': isActive == 'sj' }">司机</li>
            </ul>
        </div>
    </div>
</template>

<script>
import data from '@/mock/data'
import { getTime } from '@/tools/tools'
export default {
    name: 'home',
    components: {
    },
    data() {
        return {
            searchVal: '',
            dataList: data.filmDataList,
            isActive: 'ys',
        }
    },
    methods: {
        getTimes(time) {
            return getTime(time)
        },
        handleClickNav(e) {
            if (e.target.id == 'ys') {
                this.isActive = 'ys'
                this.dataList = data.filmDataList
            } else if (e.target.id == 'gj') {
                this.isActive = 'gj'
                this.dataList = data.toolsDataList
            } else if (e.target.id == 'sj') {
                this.isActive = 'sj'
                this.dataList = data.driverDataList
            }
        },
        handleClickTo(rows) {
            console.log(rows,'rows');
            localStorage.setItem('rows', JSON.stringify(rows))
            this.$router.push({
                path: "/detail",
            })

        }
    }


}
</script>

<style lang="less" scoped>
.home {
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100vh;
}

.content {
    // border: 1px solid red;
    width: 100%;
    // height: calc(100% - 20px);
    background: #ededed;
    padding-bottom: 10px;

    .content_center {
        width: 95%;
        margin: 0 auto;
        padding-top: 20px;

        .notice {
            margin-top: 10px;
            background: #fff;
            padding: 10px 8px;
            border-radius: 4px;
            margin-bottom: 10px;
        }

        .software-item {
            display: flex;
            align-items: center;
            justify-content: space-between;
            // border: 1px solid red;
            // width: 100%;
            background: #fff;
            padding: 20px 15px;
            border-bottom: 1px solid #f7f7f7;
            overflow: hidden;

            .software-item-left {
                // border: 1px solid red;
                width: 80%;
                // float: left;
            }

            .software-item-right {
                // border: 1px solid red;
                // width: 20%;
                // float: right;
            }
        }

    }
}

.mask {
    width: 100%;
    height: 60px;
}

.nav {

    ul {
        position: fixed;
        bottom: 0;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 100%;
        background: #fff;

        // padding: 5px 0;
        li {
            // font-size: 8px;
            // border: 1px solid red;
            height: 60px;
            width: 80px;
            text-align: center;
            line-height: 60px;

            // &:nth-child(1) {
            //     color: #409EFF;
            // }
        }

    }
}

.active {
    color: #409EFF;
}
</style>